<template>
    <v-container>
        <v-row>
            <v-card
            v-for="(item, i) in products"
            :key="i"
            :loading="loading"
            class="mx-auto my-12"
            max-width="300"
            >
                <template slot="progress">
                    <v-progress-linear
                    color="deep-purple"
                    height="10"
                    indeterminate
                    ></v-progress-linear>
                </template>
                <v-hover v-slot:default="{ hover }">

                    <v-img
                        height="200"
                        src="https://static.wixstatic.com/media/59192f_d159e2f2077b41618e02b66de98ce172~mv2.jpg/v1/fill/w_500,h_500,al_c,q_85/59192f_d159e2f2077b41618e02b66de98ce172~mv2.webp"
                    >
                    <v-expand-transition>
                        <div v-if="hover" class="d-flex transition-fast-in-fast-out darken-2 v-card--reveal display-3 white--text" style="height: 100%; background-color: rgb(187, 162, 87);"> $18.50 </div>
                    </v-expand-transition>
                    </v-img>
                </v-hover>
                <v-card-text style="position: relative;" class="pb-0">
                    <v-btn absolute color="red" class="white--text mt-n8" icon medium right top>
                        <v-icon>mdi-heart</v-icon>
                    </v-btn>
                </v-card-text>
                <v-card-title class="pt-0 pb-0">{{item.name}}</v-card-title>

                <v-card-text class="pb-0">
                    <v-row
                        align="center"
                        class="mx-0"
                    >
                        <v-rating
                        :value="4.5"
                        color="amber"
                        dense
                        half-increments
                        size="14"
                        readonly
                        class="mt-0"
                        ></v-rating>

                        <div class="grey--text ml-4">
                        4.5 (413)
                        </div>
                    </v-row>

                    <div class="my-4 subtitle-1 mb-0" style="text-align: right">
                        ${{item.price}}
                    </div>
                </v-card-text>

                <v-divider class="mx-4 ml-0 mr-0 mt-0 mb-0"></v-divider>

                <v-card-actions style="background-color:rgb(252, 249, 237)">
                    <v-btn
                    color="black"
                    to='/shop/eyelashes-products'
                    >
                    Ver
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn
                    color="black"
                    to='/shop/eyelashes-products'
                    >
                    Add to Cart
                    </v-btn>
                </v-card-actions>
            </v-card>

        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Products',
    data() {
        return {
            products: [],
        }
    },
    methods: {
        getProducts: async function (collectionId) {
            axios.get('api/v1.0/products', collectionId)
            .then(res => {
                // this.collections = res.data.products.rows;
                console.log(res);
            }).catch(err => console.log(err))
        }
    },
    created() {
        this.getProducts(this.$route.params.id);
    },
}
</script>