<template>
    <v-container fluid>

    <v-card class="mx-auto" tile>
    <v-img height="200" src="https://www.missbrowbeauty.com/wp-content/uploads/2020/04/eyelash.png"></v-img>

    <v-row>
        <v-col>
            <h3 style="text-align: end" class="pr-2"><b><i>Eyelashes Products</i></b></h3>
            <v-divider class="mt-0 mb-0 pb-0"></v-divider>
            <v-tabs class="my-tabs" vertical color="rgb(187, 162, 87)">

            <v-tab
                v-for="(item, i) in collections"
                :key="i"
                :to="`/collections/${item.id}/products`"
            >
                {{item.name}}
            </v-tab>

            <v-tab-item v-for="(item, i) in collections" :key="i" :value="`/collections/${item.id}/products`" :transition="false" :reverse-transition="false">
                <router-view></router-view>
            </v-tab-item>

        </v-tabs>
        </v-col>

    </v-row>



    </v-card>

    </v-container>


</template>

<script>
import axios from 'axios'
export default {

    name: 'Collections',
    data() {
        return {
            collections: [],
        }
    },
    methods: {
        getCollections: async function () {
            axios.get('api/v1.0/collections')
            .then(res => {
                this.collections = res.data.collections.rows;
                console.log(res.data.collections.rows);
            }).catch(err => console.log(err))
        }
    },
    created() {
        this.getCollections();
    },
}
</script>

<style>
    div.my-tabs [role="tab"] {
        justify-content: flex-start;
    }
    .v-tab {
        text-transform: none !important;
    }
    .tab-item-wrapper {
        /* vuetify sets the v-tabs__container height to 48px */
        height: calc(100vh - 40px)
    }
</style>