import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Activity from '../components/Activity.vue'
import UserAccount from '../components/UserAccount.vue'
import Messages from '../components/Messages.vue'
import Profile from '../components/Profile.vue'
import Shop from '../components/Shop.vue'
import Categories from '../components/Categories.vue'
import CoursesKitsProducts from '../components/CoursesKitsProducts.vue'
import EyebrowsProducts from '../components/EyebrowsProducts.vue'
import EyelashesProducts from '../components/EyelashesProducts.vue'
import LipsProducts from '../components/LipsProducts.vue'
import PersonalInfo from '../components/PersonalInfo.vue'
import Collections from '../components/Collections.vue'
import Products from '../components/Products.vue'
import Inks from '../components/Inks.vue'



Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/collections',
    name: 'Collections',
    component: Collections,
    children: [
      { path: ':id/products', name: 'products', component: Products},
    ]
  },
  {
    path: '/shop',
    name: 'Shop',
    redirect: { name: 'categories'},
    component: Shop,
    children: [
      { path: 'categories', name: 'categories', component: Categories},
      { path: 'kits-products', name: 'kitsProducts', component: CoursesKitsProducts},
      { path: 'eyebrows-products', name: 'eyeBrowsProducts', component: EyebrowsProducts},
      { path: 'eyelashes-products', name: 'eyelashesProducts', component: EyelashesProducts,
        children: [
          { path: 'inks', name: 'inks', component: Inks},
        ]
      },
      { path: 'lips-products', name: 'lipsProducts', component: LipsProducts},

    ]
  },
  {
    path: '/profile/:id',
    name: 'Profile',
    component: Profile,
    children: [
      { path: 'activity', component: Activity },
      { path: 'account', redirect: { name: 'personal' }, component: UserAccount,
        children: [
          { path: 'personal', name: 'personal', component: PersonalInfo},
        ]},
      { path: 'messages', component: Messages }
    ]
  },
  {
    path: '/signup',
    name: 'Signup',
    component: () => import(/* webpackChunkName: "signup" */ '../views/Signup.vue')
  },
  {
    path: '/auth/signin',
    name: 'Signin',
    component: () => import(/* webpackChunkName: "signin" */ '../views/Signin.vue')
  },
  {
    path: '/cart',
    name: 'cart',
    component: () => import(/* webpackChunkName: "cart" */ '../views/Cart.vue')
  },
  {
    path: '/categories',
    name: 'categories',
    component: () => import(/* webpackChunkName: "categories" */ '../views/Categories.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
